<template>
  <section>
    <el-form class="toolbar" label-width="100px" style="margin-top:10px;padding-top:20px;">
      <el-row>
        <el-col :span="6">
          <el-form-item label="">
            <el-input v-model="account"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="qq">
            <el-input v-model="qq"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="">
            <el-input v-model="wx"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="">
            <el-input v-model="inviteAccount"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="">
            <el-select style="width:100%" v-model="farPlace" placeholder="">
              <el-option label="1%" value="1"></el-option>
              <el-option label="2%" value="2"></el-option>
              <el-option label="2%" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item>
            <el-button type="primary" @click="shousuo"></el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-table
      highlight-current-row
      :data="dataList"
      
      style="width: 100%;"
     
    >
      <el-table-column align="center" prop="webSiteName" label=""></el-table-column>
      <el-table-column align="center" prop="account" label=""></el-table-column>
      <el-table-column align="center" prop="farPlace" label=""></el-table-column>
      <el-table-column align="center" prop="inviteCode" label=""></el-table-column>
      <el-table-column align="center" prop="qq" label="qq"></el-table-column>
      <el-table-column align="center" prop="wx" label=""></el-table-column>
      <el-table-column align="center" prop="vip2" label=""></el-table-column>
      <el-table-column width="200px" align="center" label="vip">
        <template slot-scope="scope">
          <el-button type="small" @click="setVIP(scope,scope.row)"></el-button>
        </template>
      </el-table-column>

      <!-- <el-table-column align="center" label="">
        <template slot-scope="scope">{{scope.row.money/100}} </template>
      </el-table-column>-->
    </el-table>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-select v-model="value" placeholder="" @change="changeDown">
        <el-option label="" value="0"></el-option>
        <el-option label="vip" value="1"></el-option>
        <el-option label="vip" value="2"></el-option>
      </el-select>
      <el-row :span="24" :style="{marginTop:15+'px'}" v-show="isShowForm">
        <el-row :span="24">
          <el-col :span="8" >
            <div class="demo-input-suffix" style="text-align:right">
              <span>0.5kg()</span>
              <el-input
                :style="{width:150+'px'}"
                placeholder=""
                prefix-icon="el-icon-search"
                v-model="superVipForm.nearPrice1"
              ></el-input>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="demo-input-suffix" style="text-align:right">
              <span>0.5-1kg()</span>
              <el-input
                :style="{width:150+'px'}"
                placeholder=""
                prefix-icon="el-icon-search"
                v-model="superVipForm.nearPrice2"
                align="right"
              ></el-input>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="demo-input-suffix" style="text-align:right">
              <span>()</span>
              <el-input
                :style="{width:150+'px'}"
                placeholder=""
                prefix-icon="el-icon-search"
                v-model="superVipForm.nearPrice3"
              ></el-input>
            </div>
          </el-col>
        </el-row>
        <el-row :span="24" :style="{marginTop:10+'px'}">
          <el-col :span="8">
            <div class="demo-input-suffix" style="text-align:right">
              <span :style="{}">0.5kg()</span>
              <el-input
                :style="{width:150+'px'}"
                placeholder=""
                prefix-icon="el-icon-search"
                v-model="superVipForm.farPrice1"
              ></el-input>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="demo-input-suffix" style="text-align:right">
              <span >0.5-1kg()</span>
              <el-input
                :style="{width:150+'px'}"
                placeholder=""
                prefix-icon="el-icon-search"
                v-model="superVipForm.farPrice2"
              ></el-input>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="demo-input-suffix" style="text-align:right">
              <span :style="{}">()</span>
              <el-input
                :style="{width:150+'px'}"
                placeholder=""
                prefix-icon="el-icon-search"
                v-model="superVipForm.farPrice3"
              ></el-input>
            </div>
          </el-col>
        </el-row>
      </el-row>
      
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="updateVIP(currentIndex,row,value)"
        > </el-button>
      </span>
    </el-dialog>
    <div style="padding:20px;text-align:center;">
      <el-pagination
        background
        :current-page="page"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      total: 0,
      page: 1,
      size: 10,
      dataList: [],
      account: "",
      qq: "",
      wx: "",
      inviteAccount: "",
      farPlace: "",
      dialogVisible: false,
      value: "",
      currentIndex: 0, //
      row: {},
      isShowForm: false,
      superVipForm: {
        nearPrice1: "",
        nearPrice2: "",
        nearPrice3: "",
        farPrice1: "",
        farPrice2: "",
        farPrice3: ""
      }
    };
  },
  methods: {
    changeDown() {
      console.log(this.value)
      if (this.value == 2) {
        this.isShowForm = true;
      } else {
        this.isShowForm = false;
      }
    },
    setVIP(scope, row) {
      this.dialogVisible = true;
      console.log(scope, row);
      this.currentIndex = scope.$index;
      this.row = row;
    },
    checkNumber(value) {
      var reg = /^\d+(\.\d{0,2})?$/
      return reg.test(value)
    },
    updateVIP(currentIndex, row, value) {
      let config = {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      };
      let params = new FormData();
      console.log(value);
      if (value == 2) {
        for(var key in this.superVipForm){
          if(this.superVipForm[key] === ''){
            this.$message({
              message: ''
            })
            return
          } else if (!this.checkNumber(this.superVipForm[key])){
            this.$message({
              message: ''
            })
            return
          } else {
            params.append(key,this.superVipForm[key]*100);
          }
        }
      }
      
      params.append("userId", row.id);
      params.append("vip", value);
      console.log(params);

      this.axios.put("/user/vip", params, config).then(res => {
        alert("");
        this.dialogVisible = false
        console.log(currentIndex, value, res.data.data);

        var vip = res.data.data.vip 
        if (vip == 0) {
          res.data.data.vip2 = "";
        } else if (vip == 1) {
          res.data.data.vip2 = "vip";
        } else if (vip == 2) {
          res.data.data.vip2 = "vip";
        }

        this.dataList.splice(currentIndex, 1, res.data.data);

        console.log(this.dataList[currentIndex]);
        // this.shousuo()
        this.value = ''
      });
    },
    shousuo: function() {
      let that = this;
      this.axios({
        method: "GET",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        url:
          "/user?page=" +
          (that.page - 1) +
          "&size=" +
          that.size +
          "&account=" +
          that.account +
          "&qq=" +
          that.qq +
          "&wx=" +
          that.wx +
          "&inviteAccount=" +
          that.inviteAccount +
          "&farPlace=" +
          that.farPlace
      }).then(function(response) {
        if (response.data.code == 200) {
          that.dataList = response.data.data.list;

          that.total = response.data.data.totalCount;

          that.dataList.forEach(item => {
            if (item.vip == 0) {
              item.vip2 = "";
            } else if (item.vip == 1) {
              item.vip2 = "vip";
            } else if (item.vip == 2) {
              item.vip2 = "vip";
            }
          });
        }
      });
    },
    vip2Text(vip) {
      let text = "";
      switch (vip) {
        case 0:
          text = "";
          break;
        case 1:
          text = "vip";
          break;
        case 2:
          text = "vip";
          break;
      }
      return text;
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.size = val;
      this.shousuo();
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.page = val;
      this.shousuo();
    }
  },

  created() {
    this.shousuo();
  }
};
</script>



